import React, {useContext} from "react";
import {Fade, Slide} from 'react-reveal';
import "../educationCard/EducationCard.scss";
import StyleContext from "../../contexts/StyleContext";

export default function EligibilityCard({eligible}) {
    const {isDark} = useContext(StyleContext);

    if(!eligible.logo)
        console.error(`Image of ${eligible.title} is missing in education section`);
    return (
        <div>
            <Fade right duration={1000}>
                <div className="education-card card-margin-bottom">
                    {eligible.logo && (
                        <div className="education-card-left">
                            <img src={eligible.logo} className="education-roundedimg" alt="csc" />
                        </div>
                    )}
                    <div className="education-card-right">
                        <h5 className="education-text-school">{eligible.title}</h5>

                        <div className="education-text-details">
                        <h5
                            className={
                            isDark
                                ? "dark-mode education-text-subHeader"
                                : "education-text-subHeader"
                            }
                        >
                            {eligible.subHeader}
                        </h5>
                        <p
                            className={`${
                            isDark ? "dark-mode" : ""
                            } education-text-duration`}
                        >
                            {eligible.taken}
                        </p>
                        <p className="education-text-desc">{eligible.desc}</p>
                        </div>
                    </div>
                </div>
            </Fade>
            <Slide left duration={2000}>
                <div className="education-card-border"></div>
             </Slide>
        </div>
    )
}