import React, {useContext} from "react";
import "./Creations.scss";
import VideoCard from "../../components/videoCard/VideoCard";
import {videosSection} from "../../portfolio";
import {Fade} from "react-reveal";
import StyleContext from "../../contexts/StyleContext";

export default function Creations() {
  const {isDark} = useContext(StyleContext);
  if (!videosSection.display) {
    return null;
  }
  return (
    <Fade bottom duration={1000} distance="20px">
      <div className="main" id="videos">
        <div className="video-main-div">
          <div className="video-header">
            <h1
              className={
                isDark
                  ? "dark-mode heading achievement-heading"
                  : "heading achievement-heading"
              }
            >
              {videosSection.title}
            </h1>
            <p
              className={
                isDark
                  ? "dark-mode subTitle achievement-subtitle"
                  : "subTitle achievement-subtitle"
              }
            >
              {videosSection.subtitle}
            </p>
          </div>
          <div className="video-cards-div">
            {videosSection.videoCard.map((vid, i) => {
              return (
                <VideoCard
                  key={i}
                  isDark={isDark}
                  videoInfo={{
                    title: vid.title,
                    description: vid.subtitle,
                    video: vid.url,
                    footer: vid.footerLink
                  }}
                />
              );
            })}
          </div>
        </div>
      </div>
    </Fade>
  );
}
