import React, {useContext} from "react";
import "../education/Education.scss";
import { infographicsSection } from "../../portfolio";
import StyleContext from "../../contexts/StyleContext";
import {
  Accordion,
  AccordionItem,
  AccordionItemHeading,
  AccordionItemButton,
  AccordionItemPanel
} from "react-accessible-accordion";
// import "react-accessible-accordion/dist/fancy-example.css";
import './Infographics.scss';

export default function Infographics() {

    const {isDark} = useContext(StyleContext);

    if(!infographicsSection.display){
        return null;
    }
    return (
        <div className="education-section" id="infographics">
            <h1 className="education-heading">{infographicsSection.title}</h1>
            <Accordion allowZeroExpanded>
                {infographicsSection.infographicsAccordion.map((infographic, i) => {
                    return (<AccordionItem key={i}>
                        <AccordionItemHeading>
                            <AccordionItemButton className={isDark ? "accordion__button__dark" : "accordion__button"}>
                                {infographic.title}
                            </AccordionItemButton>
                        </AccordionItemHeading>
                        <AccordionItemPanel>
                            <object data={infographic.url} type="application/pdf" width="100%" height={"650px"}>
                                <p>Alternative text - include a link <a href={infographic.alt_link}>to the PDF!</a></p>
                            </object>
                        </AccordionItemPanel>
                    </AccordionItem>)
                })}
            </Accordion>
        </div>
    )
}