import React from "react";
import '../education/Education.scss';
import EligibilityCard from "../../components/eligibilityCard/EligibilityCard";
import { eligbilityInfo } from "../../portfolio";

export default function Eligibility() {
    if(eligbilityInfo.display){
        return (
            <div className="education-section" id="eligibility">
                <h1 className="education-heading">Eligibility</h1>
                <div className="education-card-container">
                    {eligbilityInfo.eligibility.map((elig, index) => {
                        return <EligibilityCard key={index} eligible={elig} />
                    })}
                </div>
            </div>
        );
    }
    return null;
}